/*HTML*/
html, body, #root {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-family: sans-serif;
  width: 100%;
  height: 100%;
  overflow: hidden; }

h1, h2, h3, h4, h5, h6 {
  color: #313131;
  font-family: Roboto; }

p {
  font-size: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-top: 1rem;
  margin-bottom: 1rem; }

strong {
  color: #313131; }

/* COMPONENTS*/
.App {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-areas: "header header header header" "nav-bar main main main";
  overflow: hidden; }

.Header {
  background: -webkit-linear-gradient(#2b2929, #434343);
  background: linear-gradient(#2b2929, #434343);
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
  box-sizing: border-box;
  padding: 0.2rem;
  font-size: 1.3rem;
  color: beige;
  grid-area: header;
  width: 100%;
  height: 100%;
  min-height: 60px; }
  .Header a {
    display: block;
    text-decoration: none;
    width: 100%;
    padding: 0.5rem 0; }
  .Header a:hover {
    background-color: rgba(0, 0, 0, 0.5);
    color: rgba(255, 255, 255, 0.5); }
  .Header .Header__Logo {
    left: calc(30px + 1rem + 1.5rem);
    width: 75px;
    height: auto;
    margin-top: 6px; }
  .Header .Header__link {
    float: right;
    margin: 0;
    padding: 0;
    max-width: 30px;
    text-decoration: none;
    color: beige; }
  .Header .Header__link:visited {
    text-decoration: none; }
  .Header .Header__link:hover {
    color: beige;
    background: none; }

.Hamburguer {
  display: inline-block;
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  padding: 0;
  margin: 0; }
  .Hamburguer .Hamburguer__icon {
    position: relative;
    width: 100%;
    height: 100%;
    color: #ececec;
    background: transparent;
    -webkit-transition: all .2s;
    transition: all .2s; }
    .Hamburguer .Hamburguer__icon .Hamburguer__icon__line {
      width: 100%;
      height: 5px;
      border-radius: 3px;
      margin-bottom: 3px;
      background: currentColor; }
    .Hamburguer .Hamburguer__icon :active {
      cursor: pointer; }
  .Hamburguer :hover {
    color: #5497ca;
    cursor: pointer; }
  .Hamburguer :active .Hamburguer__icon__line {
    color: #3874a1;
    box-shadow: inset 0 0 1px black;
    cursor: pointer; }

.Active {
  display: inline-block;
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  padding: 0;
  margin: 0; }
  .Active .Hamburguer__icon {
    position: relative;
    width: 100%;
    height: 100%;
    color: #ceffc4;
    background: transparent;
    -webkit-transition: all .2s;
    transition: all .2s; }
    .Active .Hamburguer__icon .Hamburguer__icon__line {
      width: 100%;
      height: 5px;
      border-radius: 3px;
      margin-bottom: 3px;
      background: currentColor; }
    .Active .Hamburguer__icon :active {
      cursor: pointer; }
  .Active :hover {
    color: #5497ca;
    cursor: pointer; }
  .Active :active .Hamburguer__icon__line {
    color: #3874a1;
    box-shadow: inset 0 0 1px black;
    cursor: pointer; }

.Side_bar {
  background: -webkit-linear-gradient(#434343, #2b2929);
  background: linear-gradient(#434343, #2b2929);
  color: #f6f8fa;
  grid-area: nav-bar;
  display: inline-block;
  min-width: 100px;
  max-width: 250px;
  width: 100%;
  max-height: 100%;
  box-sizing: border-box;
  padding: 1rem;
  text-align: center;
  font-size: 1.2rem;
  overflow-y: scroll;
  margin: 0;
  -webkit-transition: all .2s;
  transition: all .2s; }
  .Side_bar .clicked {
    color: #ceffc4;
    background: -webkit-radial-gradient(#434343, #2b2929);
    background: radial-gradient(#434343, #2b2929); }
  .Side_bar ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .Side_bar ul li {
      width: 100%;
      display: block;
      float: left;
      cursor: pointer;
      border-bottom: 2px solid grey;
      border-top: 2px solid #333;
      margin-top: 0.5rem;
      padding-bottom: 0.2rem;
      border-radius: 5px;
      background: -webkit-linear-gradient(#2b2929, #434343);
      background: linear-gradient(#2b2929, #434343);
      -webkit-transition: all .2s;
      transition: all .2s; }
      .Side_bar ul li:hover {
        color: #5497e4;
        border-bottom: 2px solid grey;
        background: -webkit-linear-gradient(#434343, #2b2929);
        background: linear-gradient(#434343, #2b2929); }
      .Side_bar ul li:active {
        padding-top: 0.1rem;
        border-bottom: 1px solid #0366d6;
        background: -webkit-linear-gradient(#434343, #2b2929);
        background: linear-gradient(#434343, #2b2929); }

.Main {
  grid-area: main;
  display: inline-block;
  width: 100%;
  max-height: 100vh;
  margin: 0;
  box-sizing: border-box;
  background-image: url("../img/noise.png"), -webkit-linear-gradient(left, #2C5364, #203A43, #0F2027);
  background-image: url("../img/noise.png"), linear-gradient(to right, #2C5364, #203A43, #0F2027);
  text-align: center;
  color: black;
  font-size: 1rem;
  letter-spacing: .05rem;
  overflow-Y: scroll; }
  .Main .title {
    text-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
    margin-bottom: -1rem; }
  .Main .subtitle {
    text-shadow: 0 0 5px rgba(68, 67, 66, 0.3);
    margin-bottom: -1rem;
    text-align: left;
    margin-left: 2rem;
    font-family: fantasy; }
  .Main .Main__content {
    position: relative;
    width: 80%;
    margin: 1.5rem auto;
    background-color: #fafbfc;
    padding: 1rem;
    border-radius: 15px;
    box-shadow: 0 0 15px 5px rgba(15, 32, 39, 0.5); }
    .Main .Main__content hr {
      margin-top: 2rem; }
    .Main .Main__content .copyright {
      color: #504f4f;
      font-size: 1rem; }
  .Main .Blog_title {
    font-family: 'Charmonman', cursive;
    position: absolute;
    width: 50%;
    height: auto;
    margin: 0 auto;
    top: 12%;
    z-index: 5;
    left: 0;
    right: 0;
    padding: 1rem;
    color: beige;
    text-shadow: 0 0 5px #0F2027;
    border: goldenrod px solid;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.5);
    box-shadow: 0 0 10px 0 #333, 0 0 10px 3px #1d1c1c inset; }
    .Main .Blog_title h1 {
      font-family: "calisto-mt";
      color: beige;
      font-weight: bold;
      text-shadow: -3px 3px 0 rgba(87, 153, 57, 0.507);
      font-size: 1.8rem;
      letter-spacing: 0.4rem;
      margin-bottom: 1rem; }
    .Main .Blog_title .Blog_title_subtitle {
      font-family: "calisto-mt";
      font-size: 1.5rem;
      line-height: 1;
      margin-top: -0.5rem;
      margin-bottom: 0.6rem;
      font-style: italic; }
    .Main .Blog_title h2:nth-child(3) {
      font-family: "calisto-mt";
      color: beige;
      font-weight: 100;
      letter-spacing: 0.8rem;
      font-size: 1.7rem;
      text-shadow: -2px 2px 0 rgba(87, 153, 57, 0.507);
      font-style: italic;
      margin: 0 0 0 0;
      text-decoration: underline; }
    .Main .Blog_title h2:nth-child(4) {
      font-family: "calisto-mt";
      color: #cdffb6;
      letter-spacing: .3rem;
      font-weight: bold;
      text-shadow: -3px 3px 0 rgba(87, 153, 57, 0.507);
      font-size: 2.1rem;
      margin-bottom: 1rem;
      margin-top: 1rem;
      text-transform: uppercase; }
    .Main .Blog_title h3 {
      font-family: "calisto-mt";
      color: beige;
      text-shadow: 0 0 8px rgba(87, 153, 57, 0.507);
      font-style: oblique; }
  .Main .img {
    width: 100%;
    height: auto;
    border-radius: 15px;
    -webkit-filter: brightness(110%);
            filter: brightness(110%); }

.Footer {
  font-size: 1rem; }

/*Pages*/
.MyProjects .MyProjects__Project .MyProjects__Project__title {
  margin-bottom: 1rem; }

.MyProjects .MyProjects__Project .MyProjects_Project__PhotoList {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-evenly; }
  .MyProjects .MyProjects__Project .MyProjects_Project__PhotoList img {
    width: 60%;
    display: inline-block; }
  .MyProjects .MyProjects__Project .MyProjects_Project__PhotoList .MyProjects__Project__list {
    width: 40%;
    display: inline-block; }
    .MyProjects .MyProjects__Project .MyProjects_Project__PhotoList .MyProjects__Project__list .MyProjects__Project__list__item {
      display: flex;
      align-items: center;
      margin-left: 1rem;
      color: #313131;
      font-size: 0.9rem; }
  .MyProjects .MyProjects__Project .MyProjects_Project__PhotoList .MyProjects__Project__description {
    width: 50%;
    margin: 1rem 0;
    padding: 0;
    justify-content: end; }
    .MyProjects .MyProjects__Project .MyProjects_Project__PhotoList .MyProjects__Project__description p {
