.About .About__photo {
  width: 15vw;
  height: 15vw;
  border-radius: 50%;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.3);
  margin-bottom: 1rem; }

.Tools .Tools__container {
  width: 100%;
  display: flex;
  align-items: stretch;
  justify-content: space-around;
  margin-top: 1rem; }
  .Tools .Tools__container .Tools__container__item {
    max-width: 50%;
    display: inline-block; }
    .Tools .Tools__container .Tools__container__item .Tools__subtitle--front__list .Tools__subtitle--front__list__item {
      display: flex;
      align-items: center;
      margin-left: 1rem;
      color: #313131;
      font-size: 0.9rem; }
    .Tools .Tools__container .Tools__container__item .Tools__subtitle--back__list .Tools__subtitle--back__list__item {
      display: flex;
      align-items: center;
      margin-left: 1rem;
      color: #313131;
      font-size: 0.9rem; }

.Photos .Photos__item {
  margin-top: 1.5rem; }

