/*Phones*/
@media only screen and (max-width: 767px) {
  .App {
    grid-template-areas: "header" "main";
    overflow: hidden; }
  .Side_bar {
    display: block;
    width: 70vw;
    position: absolute;
    left: 0;
    top: 60px;
    bottom: 0;
    z-index: 99; }
  .Main .Main__content {
    width: 95%;
    min-height: 100vh;
    margin: 3px auto 3px auto; }
    .Main .Main__content div .Blog .img {
      width: 100%;
      min-height: 100%; }
    .Main .Main__content div .Blog .Blog_title {
      background: none;
      border-radius: 0;
      box-shadow: none; }
      .Main .Main__content div .Blog .Blog_title p {
        display: none; }
      .Main .Main__content div .Blog .Blog_title h2 {
        display: none; }
      .Main .Main__content div .Blog .Blog_title h3 {
        display: none; }
    .Main .Main__content div #About .About__photo {
      width: 30vw;
      height: 30vw; }
    .Main .Main__content div #MyProjects .MyProjects__Project .MyProjects_Project__PhotoList .MyProjects__Project__list p {
      padding-left: 0; } }

