.Side_bar {
    background: $color-2;
    color: rgb(246, 248, 250);
    grid-area: nav-bar;
    display: inline-block;
    min-width: 100px;
    max-width: 250px;
    width: 100%;
    max-height: 100%;
    box-sizing: border-box;
    padding: 1rem;
    text-align: center;
    font-size: 1.2rem;
    overflow-y: scroll;
    margin: 0;
    transition: all .2s;

    .clicked {
        color: rgb(206, 255, 196);
        background: $gradient-2;
    }

    ul{
        list-style: none;
        margin: 0;
        padding: 0;
        
        li{
            width: 100%;
            display: block;
            float: left;
            cursor: pointer;
            border-bottom: 2px solid grey;
            border-top: 2px solid #333;
            margin-top:0.5rem;
            padding-bottom :0.2rem;
            border-radius: 5px;
            background: $gradient-0;
            transition: all .2s;

            &:hover {
                color: rgb(84, 151, 228);
                border-bottom: 2px solid grey;
                background: $gradient-1;
            
            }

            &:active {
                padding-top:0.1rem;
                border-bottom: 1px solid rgb(3, 102, 214);
                background: $color-2;
            }
        }
    }
}
